<template>
  <div class="todo-item-container">
    <div class="operate-btn">
      <div class="left-btn">
        <el-checkbox v-model="checked" class="operate-checkbox" @change="selectAll">Select All</el-checkbox>
        <el-button size="small" :disabled="isDisabled" @click="processing" type="primary"> Process </el-button>
        <el-button
          v-if="needDelegateBtn"
          size="small"
          plain
          type="primary"
          :disabled="isDisabled || !hasPerm('WF_MYTASK_MYTASK_DELEGATE_BTN')"
          @click="delegate"
        >
          Delegate
        </el-button>
      </div>
      <div class="right-btn">
        <el-select
          size="small"
          :clearable="false"
          v-model="sortCondition"
          @change="handleCommand"
          :placeholder="$t('common.placeholder.sortBy') + ''"
        >
          <el-option
            v-for="item in todoInfoList.sortData"
            :key="item.label"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </div>
    </div>
    <div class="loading-container" v-loading="searchLoading">
      <div class="item-info-list" v-for="item in infoList" :key="item.taskId">
        <div class="item-info">
          <div class="item-header">
            <div class="left-info">
              <el-checkbox @change="checkData(item)" v-model="item.isCheck">{{ item.title }}</el-checkbox>
              <div class="task-priority" :class="priorityStyle(item.taskPriority)">
                {{ priorityStyle(item.taskPriority) }}
              </div>
              <div class="task-status" :class="statusStyle(item.taskState)">{{ item.taskState }}</div>
            </div>
            <div class="right-operate-btn">
              <el-button v-if="processIsShow(item)" @click="process(item)" size="small" plain type="primary">
                Process
              </el-button>
              <el-button
                v-if="delegateIsShow(item)"
                :disabled="!hasPerm('WF_MYTASK_MYTASK_DELEGATE_BTN')"
                @click="singleDelegate(item)"
                size="small"
                plain
              >
                Delegate
              </el-button>
              <el-button
                v-if="detailIsShow(item)"
                :disabled="!hasPerm('WF_MYTASK_MYTASK_VIEW_BTN')"
                @click="detail(item)"
                size="small"
                class="detail-btn"
              >
                {{ $t('common.button.detail') }}
              </el-button>
            </div>
          </div>
          <div class="info-desc">{{ item.taskDesc }}</div>
          <div class="operate-info">
            <svg-icon icon-class="operator" class="operator"></svg-icon
            ><span class="owner">{{ item.initiatorName }}</span> <svg-icon icon-class="time" class="time"></svg-icon
            ><span>{{ item.createTime }}</span> <span class="due-to">Due to</span
            ><span class="due-time">{{ item.expireTime }}</span>
          </div>
        </div>
      </div>
    </div>
    <portal-pagination
      :total="total"
      :pageSize="pageSize"
      @pagination="handleSizeChange"
      :hidden="hasData"
    ></portal-pagination>
  </div>
</template>

<script>
import PortalPagination from '@/components/portal-pagination';
import SvgIcon from '@/components/svg-icon';
export default {
  name: 'todoInfo',
  components: {
    PortalPagination,
    SvgIcon,
  },
  props: {
    todoInfoList: {
      type: Object,
      default: () => ({}),
    },
    pageSize: {
      type: Number,
      default: 10,
    },
    sortCondition: {
      type: String,
      default: '',
    },
    checkList: {
      type: Array,
      default: () => [],
    },
    initialCheck: {
      type: Array,
      default: () => [],
    },
    needDelegateBtn: {
      type: Boolean,
      default: true,
    },
    searchLoading: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: 'myToDo',
    },
  },
  data() {
    return {
      checked: false,
      isDisabled: true,
    };
  },
  computed: {
    infoList() {
      return this.todoInfoList.todoInfoLists.map((item) => {
        this.$set(
          item,
          'isCheck',
          this.initialCheck.some((cItem) => cItem === item.taskId)
        );
        return item;
      });
    },
    hasData() {
      return this.todoInfoList.todoInfoLists.length === 0;
    },
    total() {
      return this.todoInfoList.count;
    },
    // 区分myTodo和group task
    isMyToDo() {
      return this.todoInfoList.taskType;
    },
  },
  watch: {
    infoList: {
      handler() {
        if (this.infoList.some((item) => item.isCheck)) {
          this.isDisabled = false;
        } else {
          this.isDisabled = true;
        }
      },
      deep: true,
    },
  },
  methods: {
    checkData(val) {
      this.$emit('check-change', val);
    },
    handleCommand(val) {
      this.$emit('handle-sort-conditions', val.value);
    },
    selectAll(check) {
      this.todoInfoList.todoInfoLists.forEach((item) => {
        if (check) {
          item.isCheck = true;
        } else {
          item.isCheck = false;
        }
        this.$emit('check-change', item);
      });
    },
    process(val) {
      this.$emit('processing', val);
    },
    processIsShow(val) {
      return val.taskState !== 'Paused' && val.processFlag && val.taskType !== 'Modify';
    },
    delegateIsShow(val) {
      return val.taskState !== 'Paused' && val.processFlag && val.taskType !== 'Modify' && this.isMyToDo;
    },
    detailIsShow(val) {
      return this.processIsShow(val) || val.viewFlag;
    },
    processing() {
      this.$emit('processing');
    },
    detail(val) {
      this.openNewWindow({ path: 'detail', query: val });
    },
    delegate() {
      this.$emit('delegate');
    },
    singleDelegate(val) {
      this.$emit('delegate', val);
    },
    priorityStyle(priority) {
      if (priority === '200') {
        return 'high';
      } else if (priority === '100') {
        return 'midium';
      } else {
        return 'low';
      }
    },
    statusStyle(status) {
      if (status === 'Processing') {
        return 'process';
      } else {
        return 'pause';
      }
    },
    handleSizeChange(page) {
      this.$emit('handle-size-change', page);
    },
    reset() {
      this.checked = false;
    },
  },
};
</script>

