<template>
  <div>
    <el-dialog
      title="Operation Failed"
      width="448px"
      :visible.sync="visible"
      :close-on-click-modal="false"
      :before-close="closeNoticeDialog"
      @closed="closeNoticeDialog"
    >
      <div class="notification-content">
        <label>The Tasks To Be Processed In Bulk Are Not Of The Same Type.</label>
        <div @click="showNoticeDetail = !showNoticeDetail" class="show-detail-btn">
          <span v-if="showNoticeDetail">&lt;&lt;</span><span>Show Details</span
          ><span v-if="!showNoticeDetail">&gt;&gt;</span>
        </div>
        <el-form class="notice-detail-form" v-if="showNoticeDetail" label-width="155px" label-position="left">
          <el-form-item label="Error Code:"><span>405420045</span></el-form-item>
          <el-form-item label="Exception Description:"><span>The required token is invalid</span></el-form-item>
          <el-form-item label="Exception Cause:"><span>The operator failed to be authenticated.</span></el-form-item>
          <el-form-item label="Solution:"><span>Login again.</span></el-form-item>
        </el-form>
      </div>
      <div style="text-align: right; padding-bottom: 24px">
        <button size="small" class="cancel-button" @click="closeNoticeDialog">
          {{ this.$t('common.button.cancel') }}
        </button>
        <el-button type="primary" size="small" @click="closeNoticeDialog" v-preventReClick>{{
          this.$t('common.button.confirm')
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'notificationDialog',
  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      visible: false,
      showNoticeDetail: false,
    };
  },
  watch: {
    dialogVisible: {
      handler(value) {
        this.dialogVisible;
        this.visible = value;
      },
      immediate: true,
    },
    visible: {
      handler(newVal) {
        this.$emit('update:dialogVisible', newVal);
      },
    },
  },
  methods: {
    closeNoticeDialog() {
      this.visible = false;
      this.showNoticeDetail = false;
    },
  },
};
</script>

