import cookie from './cookie';
import { MessageBox } from 'element-ui';

export default {
  downLoadFile: function (url, data, method, hasXXSRFTOKEN) {
    let xhr = new XMLHttpRequest();
    xhr.open(method || 'POST', url, true);
    xhr.setRequestHeader('Content-type', 'application/json');
    if (hasXXSRFTOKEN) {
      xhr.setRequestHeader('X-XSRF-TOKEN', cookie.get('O-XSRF-TOKEN'));
    }
    xhr.setRequestHeader('o-token', cookie.get('o-token'));
    xhr.setRequestHeader('Cache-Control', 'no-store, max-age=0, must-revalidate');
    xhr.responseType = 'blob'; // 返回类型blob
    xhr.onload = function () {
      // 定义请求完成的处理函数，请求前也可以增加加载框/禁用下载按钮逻辑
      if (this.status === 200) {
        let blob = this.response;
        let temp = xhr.getResponseHeader('content-disposition').split(';')[1].split('filename=')[1];
        let fileName = decodeURIComponent(temp);
        let zipUrl = URL.createObjectURL(blob);
        let link = document.createElement('a');
        link.href = zipUrl;
        link.download = fileName;
        link.click();
      } else {
        MessageBox.alert('Download failed.', 'Error', { type: 'error', confirmButtonText: 'Confirm' });
      }
    };
    xhr.send(JSON.stringify(data));
  },
};
