<template>
  <el-dialog
    :title="title"
    :visible.sync="visible"
    :width="width"
    class="dialog-portal"
    :class="{ 'dialog-overflow': isOverflowHeight }"
    :close-on-click-modal="closeOnClickModal"
    :before-close="handleClose"
    @opened="openedDialog"
    @closed="closedDialog"
  >
    <slot name="operation"></slot>
    <div style="padding: 0 20px">
      <el-divider class="dialog-top-divider"></el-divider>
    </div>
    <portal-form
      v-if="dialogSearchFormVisibility"
      class="dialog-search-form"
      ref="dialogSearchForm"
      :needBottomLine="needSearchFormBottomLine"
      :form-config-list="searchForm.formConfigList"
      :form-data="searchForm.formData"
      :form-rules="formRules"
      :needOperateBtn="needSearchOperateBtn"
      :searchBtnLoading="searchLoading"
      :smallAutoColumn="smallAutoColumn"
      :bigAutoColumn="bigAutoColumn"
      @common-enter-event="enterEvent"
      @reset-btn-click="reset"
      @search-btn-click="search"
    ></portal-form>
    <div v-if="dialogTableVisibility" class="table-container">
      <table-page
        ref="dialogTable"
        :is-check="dialogTable.isCheck || true"
        :is-search="tableIsSearch"
        :columns="dialogTable.columns || []"
        :has-pagination="dialogTable.hasPagination || false"
        :pagination="dialogTable.paginationConfig || {}"
        :getTableData="dialogTable.getTableData"
        :id-key="dialogTable.idKey || 'id'"
        :isSingleCheck="isSingleCheck"
        @selection-change="onSelectionChange"
        @single-selection-change="singleChange"
      ></table-page>
    </div>
    <div style="padding: 0 20px">
      <el-divider class="config-form-bottomLine" v-if="configFormBottomLine"></el-divider>
    </div>
    <portal-form
      v-if="dialogConfigFormVisibility"
      class="dialogConfigForm"
      ref="dialogConfigForm"
      :needBottomLine="false"
      :form-config-list="configForm.formConfigList"
      :form-data="configForm.formData"
      :form-rules="configFormRules"
      :needOperateBtn="needOperateBtn"
      :smallAutoColumn="configForm.configSmall || 1"
      :bigAutoColumn="configForm.configBig || 1"
    ></portal-form>
    <div slot="footer">
      <button class="cancel-button" @click.prevent="handleCancelClick">{{ cancelText }}</button>
      <el-button type="primary" size="small" v-preventReClick @click="handleConfirmClick">{{ confirmText }}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';
export default {
  name: 'portalDialog',
  components: {
    PortalForm,
    TablePage,
  },
  props: {
    title: {
      type: String,
      default: '',
    },
    closeOnClickModal: {
      type: Boolean,
      default: false,
    },
    dialogVisible: {
      type: Boolean,
      default: false,
    },
    isOverflowHeight: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: '800px',
    },
    cancelText: {
      type: String,
      default: 'Cancel',
    },
    confirmText: {
      type: String,
      default: 'Confirm',
    },
    searchForm: {
      type: Object,
      default: null,
    },
    formRules: {
      type: Object,
      default: () => ({}),
    },
    searchLoading: {
      type: Boolean,
      default: false,
    },
    smallAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    enterEvent: {
      type: Function,
      default: () => {},
    },
    // table-property
    dialogTable: {
      type: Object,
      default: () => ({}),
    },
    // configForm-property
    configForm: {
      type: Object,
      default: () => ({}),
    },
    configFormRules: {
      type: Object,
      default: () => ({}),
    },
    needSearchFormBottomLine: {
      type: Boolean,
      default: false,
    },
    configFormBottomLine: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      visible: false,
    };
  },
  computed: {
    dialogSearchFormVisibility() {
      if (this.searchForm.formConfigList === undefined) return false;
      return this.searchForm.formConfigList.length > 0 ? true : false;
    },
    dialogConfigFormVisibility() {
      if (this.configForm.formConfigList === undefined) return false;
      return this.configForm.formConfigList.length > 0;
    },
    dialogTableVisibility() {
      return this.dialogTable && this.dialogTable.columns.length > 0 ? true : false;
    },
    needSearchOperateBtn() {
      if (this.searchForm.needOperateBtn === undefined) {
        return true;
      } else if (this.searchForm.needOperateBtn) {
        return true;
      } else {
        return false;
      }
    },
    needOperateBtn() {
      if (this.configForm.needOperateBtn) {
        return this.configForm.needOperateBtn;
      } else {
        return false;
      }
    },
    tableIsSearch() {
      if (this.dialogTable.isSearch) {
        return true;
      } else {
        return false;
      }
    },
    isSingleCheck() {
      if (this.dialogTable.isSingleCheck) {
        return true;
      } else {
        return false;
      }
    },
    onSelectionChange() {
      if (this.dialogTable.onSelectionChange) {
        return this.dialogTable.onSelectionChange;
      } else {
        return () => {};
      }
    },
    singleChange() {
      if (this.dialogTable.singleChange) {
        return this.dialogTable.singleChange;
      } else {
        return () => {};
      }
    },
  },
  watch: {
    dialogVisible: {
      handler(value) {
        this.visible = value;
      },
      immediate: true,
    },
    visible: {
      handler(newVal) {
        this.$emit('show-dialog', newVal);
        this.$emit('update:dialogVisible', newVal);
      },
    },
  },
  methods: {
    handleClose() {
      this.visible = false;
    },
    handleCancelClick() {
      this.$emit('cancel-btn-click');
    },
    handleConfirmClick() {
      this.$emit('confirm-btn-click');
    },
    closedDialog() {
      this.$emit('closed-dialog');
    },
    openedDialog() {
      this.$emit('opened-dialog');
    },
    reset() {
      this.$emit('search-reset');
    },
    search() {
      this.$refs.dialogSearchForm.validate((result) => {
        if (result) {
          this.dialogTable.isSearch = true;
          this.$emit('update:searchLoading', true);
        }
      });
    },
  },
};
</script>

